.icon {
  margin-right: 0.25rem;
}

i[class^='icon-'] {
  color: var(--icon-color);
  font-size: 12px;
  vertical-align: middle;
}

svg {
  width: 24px;
  height: 24px;
  vertical-align: middle;
}

.icon-close::before {
  content: '\00D7';
}

.icon-download::before {
  content: '\1F4BE';
}

.icon-copy::before {
  content: '\1F4CB';
}

.icon-selected::before {
  content: '\203A';
}

.icon-edit::before {
  content: '\1F58B';
}

.icon-new::before {
  content: '\002B';
  font-size: 14px;
}

.icon-search::before {
  content: '\1F50D'; /* Unicode character for üîç */
}

.icon-clear::before {
  content: '\1F5D1'; /* Unicode for wastebasket */
}

.icon-settings::before {
  content: '\1F6E0'; /* Unicode for wrench */
  //content: "\2699"; // Gear
}

.icon-refresh::before {
  content: '\21BB';
}

.icon-send::before {
  content: '\1F4E9';
}

.icon-delete::before {
  content: '\274C';
}

.icon-abort::before {
  content: '\00D7';
  color: var(--red);
  //content: "\00D7"; /* Unicode for ‚úñ */
  // content: '\274C'; // ‚ùå
}

.icon-save::before {
  content: '\1F4BE'; /* Unicode for floppy */
}

.icon-menu::before {
  content: '\22EF'; /* Unicode character for three dots */
}

.icon-user::before {
  content: '\1F464'; /* Unicode for üë§ */
}

.icon-robot::before {
  content: '\1F916'; /* Unicode for ü§ñ */
}

.icon-speech::before {
  content: '\1F4AC';
}

.icon-speech2::before {
  content: '\1F5E8'; /* Unicode for üó®Ô∏è */
}

.icon-scroll-to-top::before {
  content: '\21A5';
}

.icon-scroll-to-end::before {
  content: '\21A7';
}

// Light theme
:root {
  svg rect {
    fill: var(--text-color);
    stroke: var(--bg-color);
  }

  .icon-settings rect {
    stroke: var(--text-color);
    fill: var(--bg-color);
  }
}

// Dark theme
@media (prefers-color-scheme: dark) {
  :root {
    svg rect {
      fill: var(--text-color);
      stroke: var(--bg-color);
    }
  }
}

